<template>
  <div>
    <van-button class="akm-btn" type="primary"
                @click="jump('hs')"
                block>核酸检测报告
    </van-button>
    <van-button class="akm-btn" type="primary"
                @click="jump('akm')"
                block>打开安康码
    </van-button>
    <div align="center"  class="akm-btn" >
    <van-button style="width: 200px;;" plain type="primary" @click="jump('wttxmz')">皖投天下名筑</van-button>
    </div> 
    <div align="center" class="akm-btn" >
    <van-button style="width: 200px;;" plain type="primary" @click="jump('dthtz')">地铁海棠站</van-button>
    </div> 
    <div align="center" class="akm-btn" >
    <van-button style="width: 200px;;" plain type="primary" @click="jump('dthfnz')">地铁合肥南站</van-button>
    </div> 

  </div>
</template>
<script>
export default {
  name: 'Index',
  data() {
    return {
      prefix: 'alipays://platformapi/startapp?appId=2018081661065647&page=pages%2Fservice%2Fservice%3furl%3d',
      urls: {
        hs: 'https%3A%2F%2Fakm.ahzwfw.gov.cn%2Fakm-hsjc%2Findex.html%23%2FnucleicAcidTestInfo',
        akm: 'https%3a%2f%2fakm.ahzwfw.gov.cn%2fakm-sj-user%2findex.html%3fbelongarea%3d340100%24%2fhome%3fbelongarea%3d340100%26auth%3dct',
        wttxmz: 'http%3A%2F%2Fakmcdn.bigdatahefei.com%2Fakm-user%2Findex.html%24%2FmyAkm%3FisScan%3D1%40inout%3D1%40siteId%3DA0B3A6303E4515C5E053A51FA8C0CE10%26auth%3dct',
        dthtz: 'http%3A%2F%2Fakmcdn.bigdatahefei.com%2Fakm-user%2Findex.html%24%2FmyAkm%3FisScan%3D1%40inout%3D1%40siteId%3Ddtz00060%26auth%3dct',
        dthfnz: 'http%3A%2F%2Fakmcdn.bigdatahefei.com%2Fakm-user%2Findex.html%24%2FmyAkm%3FisScan%3D1%40inout%3D1%40siteId%3Ddtz00010%26auth%3dct'
      }
    }
  },
  mounted() {
    document.title = '安康码助手'
